// Form
::placeholder {
  color: var(--color-text-gray);
}

.form {
  width: 100%;

  &__container {
    width: 100%;
    padding: 48px;
    background-color: #fff;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
    border-radius: 16px;
  }

  // Head
  &__head {
    margin-bottom: 24px;

    &_title {
      font-size: 32px;
      margin-bottom: 8px;
    }

    &_descr {
      font-size: 16px;
      line-height: 24px;
      opacity: 0.6;
    }
  }

  &__group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 16px;
  }

  // Body
  &__item {
    display: flex;
    flex-direction: column;
    text-align: left;
    position: relative;
    margin-bottom: 16px;
    &_checkbox {
      margin-bottom: 0;
    }
  }

  &__label {
    color: var(--font-default);
    color: #232c36;
    font-size: 14px;
    font-weight: 600;
    line-height: 1;
    margin-bottom: 8px;
  }

  &__input,
  &__textarea,
  &__select,
  .select__toggle {
    outline: none;
    height: 48px;
    padding: 0 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    color: var(--font-default);
    background: #ffffff;
    border: 1px solid #d3d3d3;
    border-radius: 6px !important;
    transition: all 0.3s ease 0s;

    &:focus,
    &:active {
      border: 1px solid var(--color-primary);
    }
  }

  &__select {
    display: inline-block;
    overflow: hidden;
    padding-left: 16px;
    padding-right: 25px;
    background: url("data:image/svg+xml;base64,")
      no-repeat;
    background-size: 24px 24px;
    background-position: right 8px top 12px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    opacity: 0.6;
  }

  .select__toggle {
    padding-left: 16px;
  }

  &__textarea {
    min-height: 112px;
    resize: none;
    resize: vertical;
    padding: 16px;
  }

  &__icon {
    width: 20px;
    height: 20px;
    display: inline-block;
    font-size: 20px;
    line-height: 20px;
    position: absolute;
    bottom: 15px;
    right: 16px;
    opacity: 0.3;
  }

  &__btn {
    width: 100%;
    margin-bottom: 16px;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

// Radio + Label
.options {
  display: flex;
  flex-direction: column;
  margin-top: 5px;

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__radio {
    display: none;
  }

  &__label {
    display: inline-flex;
    line-height: 140%;
    align-items: center;
    font-size: 16px;
    font-weight: 400;
    position: relative;
    cursor: pointer;

    &::before {
      content: "";
      align-self: flex-start;
      flex: 0 0 20px;
      height: 20px;
      background-color: #fff;
      border-radius: 50%;
      border: 1px solid rgba(0, 0, 0, 0.15);
      margin-right: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 4px;
      left: 4px;
      width: 12px;
      height: 12px;
      background-color: var(--color-success);
      border-radius: 50%;
      transform: scale(0);
      transition: all 0.3s ease 0s;
    }
  }
}

.options__radio:checked + .options__label::after {
  transform: scale(1);
}

// Input Checkbox
.checkbox {
  cursor: pointer;
  position: relative;

  &__input {
    display: none;

    + label {
      user-select: none;

      &::before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 18px;
        background-color: #fbfcfd;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        margin-right: 10px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50% 50%;
        position: absolute;
        top: 2px;
        left: 0;
      }
    }

    &:checked + label::before {
      border-color: var(--color-success);
      background-color: var(--color-success);
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    }

    /* стили при наведении курсора на checkbox */
    &:not(:disabled):not(:checked) + label:hover::before {
      border-color: var(--color-success);
    }

    /* стили для активного состояния чекбокса (при нажатии на него) */
    &:not(:disabled):active + label::before {
      background-color: #b3d7ff;
      border-color: #b3d7ff;
    }

    /* стили для чекбокса, находящегося в фокусе */
    &:focus + label::before {
      box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }

    /* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
    &:focus:not(:checked) + label::before {
      border-color: #80bdff;
    }

    /* стили для чекбокса, находящегося в состоянии disabled */
    &:disabled + label::before {
      background: #fbfcfd;
    }
  }

  // .checkbox__label
  &__label {
    display: inline-block;
    font-size: 15px;
    line-height: 24px;
    padding-left: 28px;
    color: rgba(var(--color-body), 0.8);
    cursor: pointer;
    position: relative;
    top: 0;
  }

  // .checkbox__link
  &__link {
    font-size: 14px;
    line-height: 24px;
    color: rgba(var(--color-body), 0.8);
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      color: var(--color-primary);
      text-decoration: none;
    }
  }
}

// Input File
.file {
  // .file__item
  &__item {
    position: relative;
  }

  // .file__input
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    font-size: 0;
    cursor: pointer;
  }

  // .file__button
  &__button {
    display: inline-flex;
    width: 100%;
    min-height: 48px;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: 1px solid var(--color-primary);
    padding: 0 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
      color: #fff;
      background-color: var(--color-primary);
    }
  }

  // .file__preview
  &__preview {
    display: inline-block;
    max-width: 100%;
    padding: 10px 0 0 0;

    img {
      max-width: 100%;
    }
  }
}

// Media

@media (max-width: 767px) {
  .form__group {
    display: block;
    grid-template-columns: inherit;
    gap: inherit;
  }

  .form__footer {
    display: block;
  }
}

@media (max-width: 480px) {
  .form__container {
    padding: 32px;
    border-radius: 16px;
  }

  .form__head_title {
    font-size: 28px;
  }
}

@media (max-width: 390px) {
  .form__container {
    padding: 24px;
    border-radius: 8px;
  }

  .form__head {
    margin-bottom: 24px;
  }
}

@media (max-width: 375px) {
  .form__head_title {
    font-size: 24px;
  }
}
